import {createRouter, createWebHistory} from 'vue-router'
import store from './store'
import jwt_decode from "jwt-decode";

const routes = [
  { 
    path: '/',
    component: () => import('./pages/landing-page.vue'),
  },
  { 
    path: '/auth',
    component: () => import('./pages/auth-page.vue'),
    beforeEnter: async (to, from) => {
      let access = to?.query?.access
      let refresh = to?.query?.refresh
      if (access) {
        localStorage.setItem('username', jwt_decode(access).username)
        localStorage.setItem('access', access)
        localStorage.setItem('refresh', refresh)
        router.push('modules')
      }
    }
  },
  { 
    path: '/reg',
    component: () => import('./pages/reg-page.vue'),
  },
  { 
    path: '/recall',
    component: () => import('./pages/recall-page.vue'),
  },
  { 
    path: '/confirm',
    component: () => import('./pages/confirm-page.vue'),
  },
  { 
    path: '/stats',
    component: () => import('./pages/stats-page.vue'),
    beforeEnter: async (to, from) => await store.dispatch('getStats')
  },
  { 
    path: '/rating',
    component: () => import('./pages/rating-page.vue'),
    beforeEnter: async (to, from) => await store.dispatch('getRating')
  },
  { 
    path: '/modules',
    component: () => import('./pages/modules-page.vue'),
    beforeEnter: async (to, from) => await store.dispatch('getModules')
  },
  { 
    path: '/lessons',
    component: () => import('./pages/lessons-page.vue'),
    beforeEnter: async (to, from) => await store.dispatch('getLessons', localStorage.getItem('moduleName'))
  },
  { 
    path: '/quiz',
    component: () => import('./pages/quiz-page.vue'),
    beforeEnter: async (to, from) => await store.dispatch('getTasks', localStorage.getItem('lessonName'))
  },
]

const router = createRouter({
  routes,
  history: createWebHistory() 
})

router.afterEach( (to, from) => window.scrollTo(0, 0) )

export default router