<template>
  <div class="modal-underlay flex" v-if='visible'>
    <div class="modal-window" :style="{ width: modalWidth }">
      <div class="material-icons modal-cross" @click='$emit("update:visible", false)'>close</div>
      <div class="modal-content">
        <slot name="content"/>
      </div>
      <br v-if="!$slots['buttons']"/>
      <div class="modal-buttons" v-if="!!$slots['buttons']">
        <slot name="buttons"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'modal-basic',
  props: {
    modalWidth: { type: String, required: false, default: "19em" },
    visible: { type: Boolean, required: false, default: true },
    buttonColor: { type: String, required: false, default: "#333" }
  },
  computed: {
    cssProps() {
      return {
        '--button-color': this.buttonColor
      }
    }
  }
}
</script>

<style lang="scss">
//* {border: 1px solid red}
.modal-underlay {
  position: fixed;
  z-index: 2;
  left: 0; top: 0;
  right: 0; bottom: 0;
  background: rgba(0,0,0,.5);
}
.modal-window {
  z-index: 3;
  position: relative;
  box-sizing: border-box;
  border-radius: 4px;
  background-color: white;
  //box-shadow: 0 2px 15px rgba(0,0,0,.3);
  box-shadow: 0 10px 18px rgba(0,0,0,.08);
}
.modal-content {
  padding: 2em 2em 1.5em 2em;
}
.modal-icon {
  width: 100%;
  margin-bottom: 15px;
  text-align: center;
  font-size: 8em !important;
}
.modal-header {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 1rem;
  font-family: 'Oswald';
  letter-spacing: 2px;
  text-transform: uppercase;
  text-align: center;
}
.modal-text {
  text-align: center;
  //font-size: .9em;
  line-height: 1.5em;
  //color: gray;
}
.modal-buttons {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  column-gap: 1em;
  padding: 1em 1.75em;
  font-size: .95em;
  background: #00000015;
  button { 
    height: 2.5em;
    background: var(--button-color);
  }
}
.modal-cross {
  position: absolute;
  right: -.5em; top: -.5em;
  padding: 5px;
  border-radius: 50%;
  box-shadow: 0 0 10px 2px rgba(0,0,0,.3);
  font-size: 2em !important;
  background: #fff;
}
</style>