import { createStore } from 'vuex'

import controller from './api/controller'
import Lesson from './classes/Lesson'
import Task from './classes/Task'
import router from './router'

export default createStore({

  state: () => ({
    modules: [],
    blocks: [],
    lessons: [],
    tasks: [],
    rating: [],
    stats: {},
    loaderVisible: false,
    modal: { visible: false },
    showTips: true,
    showMetrics: true,
  }),

  mutations: {
    setStats(state, payload) { state.stats = payload[0] },
    setRating(state, payload) { state.rating = payload },
    setModules(state, payload) { state.modules = payload },
    setLessons(state, Blocks) {
      Blocks.forEach( (block,i) => { Blocks[i].lessons = block.lessons.map(e => new Lesson(e)) } )
      state.blocks = Blocks
    },
    setTasks(state, payload) { state.tasks = payload.map(e => new Task(e)) },
    showLoader(state, payload) { state.loaderVisible = payload },
    showMessage(state, payload) {
      state.modal = payload
      state.modal.visible = true
    },
    toggleTips(state) { state.showTips = !state.showTips },
    toggleMetrics(state) { state.showMetrics = !state.showMetrics },
  },

  actions: {
    async reg(_, payload) {
      const { username, email, password } = payload
      const res = await controller.reg(username, email, password)
      router.push('confirm')
    },
    async login(_, payload) {
      const { username, password } = payload
      const res = await controller.login(username, password)
      localStorage.setItem('username', username)
      localStorage.setItem('access', res.data.accessToken)
      localStorage.setItem('refresh', res.data.refreshToken)
      router.push('/modules')
    },
    async getModules({ commit }) {
      const res = await controller.getModules()
      commit('setModules', res.data)
    },
    async getLessons({ commit }, moduleName) {
      const res = await controller.getLessons(moduleName)
      commit('setLessons', res.data)
    },
    async getTasks({ commit }, lessonName) {
      const res = await controller.getTasks(lessonName)
      commit('setTasks', res.data)
    },
    async getStats({ commit }) {
      const res = await controller.getStats()
      commit('setStats', res.data)
    },
    async getRating({ commit }) {
      const res = await controller.getRating()
      commit('setRating', res.data)
    },
  }

})