import { createApp } from 'vue'
import App from './App.vue'
import store from './store.js'
import router from './router.js'
import VueGtag from "vue-gtag"

import arrayMethods from './mixins/array-methods'
import mix from './mixins/mix-colors'

const app = createApp(App)

app.use(router)
app.use(store)
app.use(VueGtag, {
  config: { id: "G-H6NME2KP25" }
})

app.use(arrayMethods)
app.mixin(mix)
app.mount('#app')

app.config.globalProperties.$cloudinary = 'https://res.cloudinary.com/skywing/image/upload/'
app.config.globalProperties.$red = '#ff4d4d'
app.config.globalProperties.$yellow = '#e1b74d'
app.config.globalProperties.$green = '#4da64d'
app.config.globalProperties.$maxAttempts = 2


