export default class Task {
  
  constructor(task) {
    Object.assign(this, task);
    this.attempts = task.attempts ?? 0
    this.completed = !!task.attempts
    this.answer = task.answer ?? task.key.map(_ => undefined)
  }
  
  get answered() { return !this.answer.includes(undefined) }
  //get completed() { return !!this.attempts }
  get type() { return this.key[0].toString().match(/[А-Д]/i) ? "radio" : "input" }

  get booleans() { return this.answer.map((e,i) => e==this.key[i]) }
  get percentage() { return Math.round(this.booleans.sum() / this.booleans.length * 100) }
  get score() { return Math.round(this.percentage * this.weight / 100) }

}