<template>
  <loader v-if="$store.state.loaderVisible"/>
  <modal v-model:visible="$store.state.modal.visible" :modalWidth="$store.state.modal.width">
    <template #content>
      <div v-if="$store.state.modal.icon" 
       class="material-icons-outlined modal-icon" 
       :style='{ color: $store.state.modal.color }'>{{ $store.state.modal.icon }}</div>
      <div class="modal-header">{{ $store.state.modal.title }}</div>
      <div class="modal-text" v-html='$store.state.modal.text'></div>
    </template>
  </modal>
  <router-view/>
</template>

<script>
import loader from '@/components/loader.vue'
import modal from '@/components/modal-basic.vue'
import emptyLayout from '@/layouts/empty-layout.vue'
import authLayout from '@/layouts/auth-layout.vue'
import mainLayout from '@/layouts/main-layout.vue'
export default {
  components: { loader, modal, authLayout, emptyLayout, mainLayout },
  mounted() {
    window.addEventListener('orientationchange', function () {
      var originalBodyStyle = getComputedStyle(document.body).getPropertyValue('display');
      document.body.style.display='none'
      setTimeout(() => document.body.style.display = originalBodyStyle, 10)
    })
  },
  data() { return {
    modalVisible: false
  }}
}
</script>

