Array.prototype.max = function() { return Math.max(...this) }
Array.prototype.min = function() { return Math.min.apply(null, this) }
Array.prototype.sum = function() { return this.reduce( (a, b) => a + b ) }
Array.prototype.sort0 = function() { return this.sort( (a, b) => b - a ) }
Array.prototype.sort1 = function() { return this.sort( (a, b) => a - b ) }
Array.prototype.average = function() { return (this.sum() / this.length) || 0 }
Array.prototype.equals = function(arr) { return (this.length == arr.length) && this.every((e,i) => e === arr[i]) }
Array.prototype.partialSums = function() { return this.reduce( ([acc, s], e) => [acc.concat(s+e), s+e], [[],0] ) [0] }

Array.prototype.bucket = function(step, max) {
  let res = []
  for(let i = 0; i < Math.floor(max/step); i++) res.push([])
  for(let e of this) (res[Math.floor(e/step)] ?? res[res.length-1]).push(e)
  return res
}