import axios from 'axios'
import store from '../store'
import router from '../router'

const instance = axios.create({
  baseURL: process.env.VUE_APP_API_URL,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json',
    //auth: `Bearer ${localStorage.getItem('access')}`
  }
})

let timerId

instance.interceptors.request.use(

  (config) => {
    timerId = setTimeout(() => store.commit('showLoader', true), 1000)
    const access = localStorage.getItem('access')
    if (access) config.headers.auth = `Bearer ${access}`
    return config; 
  }, 

  (error) => {
    clearTimeout(timerId)
    store.commit('showLoader', false)
    return Promise.reject(error)
  }
  
)

instance.interceptors.response.use(

  (response) => {
    clearTimeout(timerId)
    store.commit('showLoader', false)
    if (response?.status == 202) store.commit('showMessage', { 
      color: '#4da64d', 
      icon: 'done', 
      title: 'Успішно', 
      text: response?.data?.message
    })
    return response;
  },

  async (error) => {
    clearTimeout(timerId)
    store.commit('showLoader', false)

    if (error.response?.status == 403) router.push('confirm')

    else if (error.response?.status == 401 && error.config) {
      try {
        const refresh = localStorage.getItem('refresh')
        if (refresh) {
          const res = await axios.get(process.env.VUE_APP_API_URL + '/refresh', { 
            headers: { refresh: localStorage.getItem('refresh') }, 
            withCredentials: true 
          })
          localStorage.setItem('access', res.data?.accessToken)
          localStorage.setItem('refresh', res.data?.refreshToken)
          return instance.request(error.config)
        }
        else store.commit('showMessage', {
          width: 'min(23em, 80%)',
          color: 'black',
          text: `
            <br/>
            <div class="material-icons-outlined auth-icon">account_circle</div>
            <h2>Створення профіля</h2>
            <p class='auth-text'>
              Наразі ви переглядаєте сторінку у гостьовому режимі. Зареєструйтесь, 
              щоб відкрити повний доступ до сайту
            </p>
            <button class='button-plain auth-button' onclick="location.href='/reg'">Реєстрація</button>
            <small>Вже зареєстровані? <a href='/auth'>Увійти!</a></small>
            <br/>
          `
        })
      }
      catch(_) {
       router.push('auth')
      } 
      throw error
    }
    else store.commit('showMessage', { 
      color: '#ff4d4d',
      title: 'Помилка',
      icon: error.response?.data?.icon || 'hourglass_empty', 
      text: error.response?.data?.message || "Немає зв'язку із сервером. Спробуйте зайти пізніше"
    })
    return Promise.reject(error)
  }

)

export default instance